import React from 'react';
import './profile.css'
import logo from '../img/profile.jpg';
export const Profile = () => {
    return (
        <>
            <div className='hs-profile-wrapper '>
                <div className='hs-profile-pichure-wrapper'>
                    <div className='hs-profile-pichure'>
                        <img className='hs-profile-logo' src={logo} alt='profile' />
                    </div>
                </div>
                <div className='hs-profile-header'>
                    <h1 className='p-name'>Anuj kaushik</h1>
                    <h2 className='p-position title'>UI Developer</h2>
                    <p className="d-flex flex-wrap justify-content-between p-contact">
                        <span className='ph'><i className="fa fa-phone" aria-hidden="true"></i>
                            <span className='text'> 8010705281</span></span>
                        <span className='ph'><i className="fa fa-phone" aria-hidden="true"></i>
                            <span className='text'> 8700217985</span></span>
                        <span className='mail'><i className="fa fa-envelope-o" aria-hidden="true"></i>
                            <span className='text'> anujkaushikit@gmail.com</span></span>
                    </p>
                </div>
            </div>
        </>);
}
export default Profile;
