import React from 'react';
import './Navigation.css';
import { NavLink } from 'react-router-dom';
export const Navigation = () => {
    return (
        <div className='hs-navigation'>
            <div class="mobile-trigger"><span></span><span></span><span></span></div>
            <div className='hs-navigation-inner-wrapper'>
                <ul className='clearfix'>
                <li className='hs-menu-item'>
                        <NavLink to='/' exact activeClassName='active'>Overview</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Profile' exact activeClassName='active'>Profile</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Experience' exact activeClassName='active'>Experience</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Education' exact activeClassName='active'>Education</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Author_Bio' activeClassName='active'>Career Objective</NavLink>
                    </li>
                    <li className='hs-menu-item'>
                        <NavLink to='/Live_project' exact activeClassName='active'>Live Project</NavLink>
                    </li>
                </ul>
            </div>
        </div>
    );
}
